import * as React from "react";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { Divider, Grid } from "@mui/material";
import { calculateAge } from "../../Function/calculateAge";
import Paper from "@mui/material/Paper";

export default function Header(props) {
	const { lang, min, children } = props;
	const updated_event = new Date("9/4/1984");
	const options = {
		month: "long",
		day: "numeric",
	};
	let updatedDate = updated_event.toLocaleDateString(lang.type, options);

	let pixels = min ? 160 : 364;

	return (
		<Paper
			sx={{
				p: 0,
				mx: 1,
				mt: 1,
				mb: 2,
				flexGrow: 1,
				borderRadius: 3,
				boxShadow: 5,
				display: "flex",
				flexDirection: { xs: "column", md: "row" },
				alignItems: "center",
				overflow: "hidden",
			}}
		>
			<Box
				component="img"
				sx={{
					height: { xs: pixels, md: "100%" },
					width: { xs: "100%", md: pixels },
					maxHeight: pixels,
					maxWidth: pixels,
					aspectRatio: "1/1",
				}}
				alt={lang.fullname}
				title={lang.fullname}
				src="https://avatars.githubusercontent.com/u/10385173?v=4"
			/>
			<Box
				sx={{
					display: "flex",
					flexDirection: "column",
					alignItems: { xs: "center", md: "flex-start" },
					m: 0,
					minWidth: pixels,
					height: { xs: "100%", md: pixels },
					width: { xs: pixels, md: "100%" },
				}}
			>
				<Grid
					item
					xs
					sx={{
						p: 0,
						my: 0,
						mx: 1,
						textAlign: "center",
						display: "block",
						width: "100%",
					}}
				>
					<Typography
						variant="h1"
						fontWeight={700}
						component="h1"
						sx={{ my: 0, mx: 0, textAlign: "center" }}
					>
						{lang.fullname}
					</Typography>
				</Grid>
				<Divider item xs sx={{ width: "100%", my: 1 }} />
				<Grid
					item
					xs
					container
					sx={{ p: 0, my: 0, mx: 1, textAlign: "center" }}
				>
					<Grid item xs={6} sx={{ m: 0 }}>
						<b>{calculateAge(9, 4, 1984)}</b> {lang.cv.Age}
					</Grid>
					<Grid item xs={6} sx={{ m: 0 }}>
						{lang.cv.Birthday}: <b>{updatedDate}</b>
					</Grid>
				</Grid>

				<Typography item xs sx={{ px: 2, py: 0 }} color="text.secondary">
					{children}
				</Typography>
			</Box>
		</Paper>
	);
}
